import { Component, OnInit, Inject, ViewContainerRef } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder, FormArray } from '@angular/forms';
import { Category } from '../../models/category.model';
import { CategoryService, UserService, CoursesService } from '../../services/service.index';
import { DOCUMENT } from '@angular/platform-browser';
import { Courses } from '../../models/course.model';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastsManager } from 'ng2-toastr';
declare function sadad();

@Component({
  selector: 'app-course-add',
  templateUrl: './course-add.component.html',
  styles: []
})

export class CourseAddComponent implements OnInit {
// check
free_course: boolean = false;
course_discount: boolean = false;
// checkend
  add: boolean = true;
  IdCourse: number;
  forma: FormGroup;
  dropdownList = [];
  dropdownSettings = {};
  selectedItems = [];
  category: Category[] = [];
  subCategory = [];
  Course: Courses[] = [];
  statuCourse = false;
  pagination = 0;
  event = document.createEvent('UIEvents');
  Meta_keywords: string;
  proDescuen = 0;
  ganacias = 0;

  constructor(
    @Inject(DOCUMENT) private _document,
    public router: Router,
    public activateRouter: ActivatedRoute,
    public _categoryService: CategoryService,
    public _userServices: UserService,
    public _courseService: CoursesService,
    private fb: FormBuilder,
    public alert: ToastsManager

    ) {
      this.activateRouter.params.subscribe((params: any) => {
        let id = params['id'];
        this.IdCourse = params['id'];
        if (id !== 'nuevo') {
          this.add = false;
          this._courseService.showOne(id).subscribe((resp: any) => {
            // tslint:disable-next-line:no-shadowed-variable
            resp.data.requirements.forEach(resp => {
              this.arrayRequi.push(this.fb.group(resp));
            });
            this.forma.patchValue({
              Course_title: resp.data.Course_title,
              Short_description: resp.data.Short_description,
              Description: resp.data.Description,
              Category: resp.data.categories[0].id,
              Level: resp.data.Level,
              Language: resp.data.Language,
              is_top_course: resp.data.is_top_course,
              Course_price: resp.data.Course_price,
              Discounted_price: resp.data.Discounted_price,
              free_course: resp.data.free_course,
              Meta_keywords: resp.data.Meta_keywords,
              Meta_description: resp.data.Meta_description,
              // Requirements: new
            });

          });
        }

      });

    }


  ngOnInit() {
    sadad();
    this.listCategory();

    this.forma = this.fb.group({
      Requirements: this.fb.array([this.addRequirements()]),
      Outcomes: this.fb.array([this.addOutcomes()]),
      Course_title : new FormControl('', Validators.required),
      Short_description: new FormControl('', Validators.required),
      Description: new FormControl('', Validators.required),
      Category: new FormControl('', Validators.required),
      Level: new FormControl('', Validators.required),
      Language: new FormControl('', Validators.required),
      is_top_course: new FormControl('', Validators.required),
      Course_price: new FormControl(''),
      Discounted_price: new FormControl(''),
      free_course: new FormControl(''),
      course_discount: new FormControl(''),
      Meta_keywords: new FormControl(Array),
      Meta_description: new FormControl('' )
    });
    // this.cargarUsuario();

}
addOutcomes() {return this.fb.group({ name })}
addRequirements() { return this.fb.group({name})}


newAddR() {this.arrayRequi.push(this.addRequirements())};
newArrayOutcomes() {this.arrayOutcomes.push(this.addOutcomes())};

removeR(index) {this.arrayRequi.removeAt(index)};
removeOutcomes(index) {this.arrayOutcomes.removeAt(index)};

get arrayRequi(){return <FormArray>this.forma.get('Requirements')};
get arrayOutcomes(){return <FormArray>this.forma.get('Outcomes')}

listCategory() {
  this._categoryService.listCategory().subscribe((resp: any) => {
    if (resp.data.length < 1) {
      this.category = null;
      return;
    }
    this.category = resp.data;
  });
}
addCourses() {
  console.log();
if (this.forma.value.Course_title.length < 1) {
  this.alert.info('please write a title', 'course title is required');
  // console.log(hola, hola1);
  this.removeClass();
  this.tab_panelValitors('CBasic_up', 'CBasic_donw', 'course_title');
  return;
}

if (this.forma.value.Short_description.length < 2) {
  this.alert.info('short Description', 'Brief description is required');
  this.removeClass();
  this.tab_panelValitors('CBasic_up', 'CBasic_donw', 'short_description');
  return;
}
console.log(this.forma.value.Description);
if (this.forma.value.Description.length < 2) {
  this.alert.info('Description', 'Is a required field');
  this.removeClass();
  this.tab_panelValitors('CBasic_up', 'CBasic_donw', 'description');
  return;
}
console.log(this.forma.value.Category.length == 0)
if (this.forma.value.Category.length == 0) {
  this.alert.info('is a required field', 'Select a category');
  this.removeClass();
  this.tab_panelValitors('CBasic_up', 'CBasic_donw', 'CategoryNNN');
  return;
}
if (this.forma.value.Level.length == 0) {
  this.alert.info('is a required field', 'Select a Level');
  this.removeClass();
  this.tab_panelValitors('CBasic_up', 'CBasic_donw', 'level_category');
  return;
}
if (this.forma.value.Language.length == 0) {
  this.alert.info('is a required field', 'Select a language');
  this.removeClass();
  this.tab_panelValitors('CBasic_up', 'CBasic_donw', 'language_made_in');
  return;
}
if (this.forma.value.Course_price.length == 0) {
  this.alert.info('Defines the status of the course', 'Select a Price');
  this.removeClass();
  this.tab_panelValitors('CPricing_up', 'CPricing_donw', 'Course_price');
  return;
}
  let Course =  new Courses(
    this.forma.value.Course_title,
    this.forma.value.Short_description,
    this.forma.value.Description,
    this.forma.value.Category,
    this.forma.value.Level,
    this.forma.value.Language,
    this.forma.value.is_top_course,
    this.forma.value.Requirements,
    this.forma.value.Outcomes,
    this.forma.value.Course_price,
    this.forma.value.Discounted_price,
    this.forma.value.free_course,
    this.forma.value.course_discount,
    this.forma.value.Meta_keywords,
    this.forma.value.Meta_description,
    this._userServices.UserData.id
  );

  if (this.add) {
    this.newCourses(Course);
    return;
  } 

this.editCourse(Course);
}

checkboxFree(val) {
  if(val == 'free') {
    this.free_course = this.forma.value.free_course;
    this.forma.value.Course_price = 0;
    this.forma.value.Discounted_price = 0;
    this.forma.value.course_discount = 0;
    return;
  }
  if (val == 'descuen') {

    this.course_discount = this.forma.value.course_discount;
    return;
  }


}
newCourses(Course) {
  this._courseService.addCourse(Course).subscribe((resp: any) => {

    // this.router.navigate(['addCourses', resp.data.id]);
    this.router.navigate(['courses']);
    this.IdCourse = resp.data.id;
    this.alert.success('Excellent! ', 'the course was added');
  });
}
editCourse(Course) {
  // let c
  this._courseService.editCourses(this.IdCourse, Course).subscribe((resp: any) => {
    this.router.navigate(['courses']);
    this.alert.success(resp.data.Course_title, 'Updated course');
  });

}
adelate(a_id_up, div_aria_donw, delete_up, delete_donw) {
  this._document.getElementById(delete_donw).setAttribute('class', 'tab-pane');
  this._document.getElementById(delete_up).setAttribute('class', 'nav-link');

  this._document.getElementById(a_id_up).setAttribute('class', 'nav-link active');
  this._document.getElementById(div_aria_donw).setAttribute('class', 'tab-pane fade show active');
}


removeClass() {
  this._document.getElementById('CFinish_donw').setAttribute('class', 'tab-pane fade');
  this._document.getElementById('CFinish_up').setAttribute('class', 'nav-link');
}

tab_panelValitors(input_up: string, tab_donw: string, idInput: string) {
  this._document.getElementById(input_up).setAttribute('class', 'nav-link active');
  this._document.getElementById(tab_donw).setAttribute('class', 'tab-pane fade show active');
  this._document.getElementById(idInput).focus();
  
  // this._document.getElementById(idInput).select();
  this._document.getElementById(idInput).blur();
}
calcularDescuento(event) {
  let PI = this.forma.value.Course_price;
  let desC = event / PI * 100;
  let des =  100 - desC;
  this.proDescuen = desC;
  this.ganacias = des;

}
}




