import { Component, OnInit, OnDestroy } from '@angular/core';
import { MenuService, UserService } from '../../services/service.index';
import { Usuarios } from '../../models/user.model';
import { Subscription, Subject } from 'rxjs';
import 'rxjs/add/operator/takeUntil';
import { ModalUploadService } from '../../component/modal/modal-upload.service';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'app-crud-users',
  templateUrl: './crud-users.component.html',
  styles: []
})
export class CrudUsersComponent implements OnInit, OnDestroy {
  protected ngUnsubscribe: Subject<void> = new Subject<void>();
  teacher: Subscription;
  student: Subscription;
  getData: any = [];
  user: Usuarios[] = [];
  loading: boolean = true;

  activeLessons: boolean = false;
	activeSections: boolean = false;
	activePeriod: boolean = true;
	activeStatus: boolean = true;
	activeTeacher: boolean = false;
	activeCategory: boolean = false;
	activePrice: boolean = false;
	activeEnrolments: boolean = false;
	activePublished: boolean = false;

	ColumnStatus: boolean = true;
	ColumnName: boolean = true;
	ColumnAuthor: boolean = true;
	ColumnCategory: boolean = true;
	ColumnPrice: boolean = true;
	ColumnLessons: boolean = true;
	ColumnSections: boolean = true;
	ColumnDemography: boolean = true;
	ColumnEnrolments: boolean = true;
	ColumnPublished: boolean = true;
  constructor(
    public _headerService: MenuService,
    public _userService: UserService,
    public _modalService: ModalUploadService,
    public alert: ToastsManager
  ) {
    _headerService.getDataRouter()
      .subscribe((resp: any) => {
        this.getData = resp;
        if (resp.sub === 'teacher') {
          this.user = null;
          this.showTeacher();
        }
        if (resp.sub === 'studen') {
          this.user = null;
          this.showStudent();
        }
      });

  }

  ngOnInit() {
    this._modalService.deleteNotification.subscribe(esp => {
      if (this.getData.title == 'Instructors') {
        this.showTeacher();
      } else if (this.getData.title == 'Students') {
        this.showStudent();
      }
    })
    this._headerService.removePreload();
  }

  showTeacher() {
    this.loading = true;
    this.teacher = this._userService.allTeacher().takeUntil(this.ngUnsubscribe).subscribe((resp: any) => {
      console.log(resp.data);
      this.user = resp.data;
      this.loading = false;
    });
  }

  showStudent() {
    this.loading = true;
    this.teacher = this._userService.allStudents().takeUntil(this.ngUnsubscribe).subscribe((resp: any) => {
      this.user = resp.data;
      console.log(resp.data);
      this.loading = false
    });
  }

  deleteUser(id) {

    if (this._userService.UserData.id == id) {
      this.alert.info('cannot be removed', 'It`s not valid');
      return;
    }
    this._modalService.mostraModalUser(id, this.getData.title);
  }
  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
    this.teacher.unsubscribe();
  }
  GenerarAchivo(type) {

  }
  updateFilter(event, type) {
    
  }
}



