import { Component, OnInit } from '@angular/core';
import { MenuService, EnrolmentService, UserService, NewEnrolmentService } from '../../services/service.index';
import { EnrolmentModel } from '../../models/enrolment.model';

@Component({
	selector: 'app-enrolment',
	templateUrl: './enrolment.component.html',
	styles: []
})
export class EnrolmentComponent implements OnInit {
	activeName: boolean = false;
	activeLessons: boolean = false;
	activeSections: boolean = false;
	activePeriod: boolean = true;
	activeStatus: boolean = true;
	activeTeacher: boolean = false;
	activeCategory: boolean = false;
	activePrice: boolean = false;
	activeEnrolments: boolean = false;
	activePublished: boolean = false;

	ColumnStatus: boolean = true;
	ColumnName: boolean = true;
	ColumnAuthor: boolean = true;
	ColumnCategory: boolean = true;
	ColumnPrice: boolean = true;
	ColumnLessons: boolean = true;
	ColumnSections: boolean = true;
	ColumnDemography: boolean = true;
	ColumnEnrolments: boolean = true;
	ColumnPublished: boolean = true;
	enroment: EnrolmentModel[] = [];
	loading:boolean = true;
	constructor(public menu: MenuService, public _modal: EnrolmentService, public _serviceEnrolment: UserService) { }

	ngOnInit() {
		this.menu.removePreload();
		this.listEneolment();
		this._modal.eventUpdate.subscribe((resp:any) => {
			console.log('hola')
			this.listEneolment();
		})
	}
	GenerarAchivo(type) {

	}
	updateFilter(value, type) {
		console.log(value, '___', type);

	}
	listEneolment() {
		this.loading = true;
		this._serviceEnrolment.allStudents().subscribe((resp:any) => {
			this.loading = false;
			this.enroment = resp.data;
		})
	}

}



